import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IPaymentTypeModel,
  IPaymentTypeRequest,
} from "../models/payment-types";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class PaymentTypesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPaymentTypeRequest) {
    return this.post<IResponse<IPaymentTypeModel>>(
      "PaymentTypeSetup/Create",
      data
    );
  }

  update(data: IPaymentTypeRequest) {
    return this.post<IResponse<IPaymentTypeModel>>(
      "PaymentTypeSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPaymentTypeModel[]>>(
      "PaymentTypeSetup/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IPaymentTypeModel[]>>("PaymentTypeSetup/GetAll");
  }

  Delete(Id: number) {
    return this.delete<IResponse>("PaymentTypeSetup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
