import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { ILoanRuleModel, ILoanRuleRequest } from "../models/loan-rules";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LoanRulesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILoanRuleRequest) {
    return this.post<IResponse<ILoanRuleModel>>("LoanRules/Create", data);
  }

  getListByLoanTypeId(params: IPageParams, loanTypeId: number) {
    return this.get<IResponseList<ILoanRuleModel[]>>("LoanRules/GetList", {
      params: {
        ...params,
        $filter: `loneTypeId eq ${loanTypeId}`,
      },
    });
  }

  getAll() {
    return this.get<IResponse<ILoanRuleModel[]>>("LoanRules/GetAll");
  }

  Delete(Id: number) {
    return this.delete<IResponse>("LoanRules/Delete", {
      params: { Id: Id },
    });
  }
  exportExcel() {
    this.getAll();
  }
}
