import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  IPayrollElementModel,
  IPayrollElementRequest,
} from "app/system-setup/payroll-setup/models/payroll-elements";
import { PayrollElementsService } from "app/system-setup/payroll-setup/services/payroll-elements.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-add-payroll-element",
  templateUrl: "./add-payroll-element.component.html",
  styleUrls: ["./add-payroll-element.component.scss"],
})
export class AddPayrollElementComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  // payrollElement: IPayrollElementModel;
  payrollElementId: number;

  constructor(private payrollElementsService: PayrollElementsService) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.payrollElementId = Number(params.get("payrollElementId")) ?? 0;
      // if (this.payrollElementId > 0 && this.CheckNoBusinessOrCompany())
      //   this.getById();
    });

    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_name: [null, [Validators.required, CustomValidator.whiteSpace]],
      variableCode: [
        null,
        [
          Validators.required,
          CustomValidator.whiteSpace,
          CustomValidator.noSpace,
          Validators.pattern(Constants.variableCode_pattern),
        ],
      ],
      element_Group_ID: [null, [Validators.required]],
      display_In_Result: [null],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.payrollElement = null;
    if (this.payrollElementId > 0 && this.checkNoBusinessOrCompany())
      this.getById();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.payrollElementsService
      .getById(this.payrollElementId)
      .subscribe((res: IResponse<IPayrollElementModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.payrollElement = res.data[0];
          this.payrollElementToEdit(res.data[0]);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  payrollElementToEdit(payrollElement: IPayrollElementModel) {
    this.form.patchValue(payrollElement);
    this.formCtrls.en_name.setValue(payrollElement.name);
    this.formCtrls.variableCode.setValue(
      payrollElement.paY_Element_Variable[0].pay_Variable.code
    );
    this.formCtrls.display_In_Result.setValue(
      payrollElement.paY_Element_Variable[0].pay_Variable.display_In_Result
    );
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IPayrollElementRequest = {
        PAY_Element: {
          ID: this.form.value.id,
          ar_Name: this.form.value.ar_Name,
          name: this.form.value.en_name,
          element_Group_ID: this.form.value.element_Group_ID,
        },
        variableCode: this.form.value.variableCode.trim(),
        display_In_Result: this.form.value.display_In_Result ?? false,
      };

      model.PAY_Element.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IPayrollElementRequest) {
    this.showLoading();
    this.payrollElementsService
      .create(model)
      .subscribe((res: IResponse<IPayrollElementModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          // this.clearForm();
          this.payrollElementId = res.data.id;
          this.formCtrls.id.setValue(this.payrollElementId);
          let url = `/payroll-setup/${this.moduleId}/payroll-elements/edit/${this.payrollElementId}`;
          this.navigateTo(url);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.payrollElement = res.data;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IPayrollElementRequest) {
    this.showLoading();
    this.payrollElementsService
      .update(model)
      .subscribe((res: IResponse<IPayrollElementModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          // this.payrollElement = res.data;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    /* if (this.payrollElementId == 0) */
    this.form.reset({ id: 0 });
    // else this.payrollElementToEdit();
    this.submitted = false;
  }
}
