import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IPayrollElementModel,
  IPayrollElementRequest,
  IPayrollElementUserModel,
  IPayrollElementUserRequest,
  IUserDeleteParams,
} from "../models/payroll-elements";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class PayrollElementsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPayrollElementRequest) {
    return this.post<IResponse<IPayrollElementModel>>(
      "PayrollElementSetup/Create",
      data
    );
  }

  update(data: IPayrollElementRequest) {
    return this.post<IResponse<IPayrollElementModel>>(
      "PayrollElementSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPayrollElementModel[]>>(
      "PayrollElementSetup/GetList",
      {
        params: {
          ...params,
          $expand: `${db.PAY_Element_Variable},${db.PAY_Element_Variable}/${db.PAY_Variable}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IPayrollElementModel[]>>(
      "PayrollElementSetup/GetAll"
    );
  }

  getById(id: number) {
    return this.get<IResponse<IPayrollElementModel[]>>(
      "PayrollElementSetup/GetAll",
      {
        params: {
          $expand: `${db.PAY_Element_Variable},${db.PAY_Element_Variable}/${db.PAY_Variable}`,
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("PayrollElementSetup/Delete", {
      params: { Id: Id },
    });
  }

  ////////// payroll element users

  createUser(data: IPayrollElementUserRequest[]) {
    return this.post<IResponse<IPayrollElementUserModel>>(
      "PayrollElementSetup/Create_Element_Users_Details",
      data
    );
  }

  getAllUsersByPayrollElement(payrollElementId: number) {
    return this.get<IResponse<IPayrollElementUserModel[]>>(
      "PayrollElementSetup/GetAll_Element_Users_Details",
      {
        params: { Element_ID: payrollElementId },
      }
    );
  }

  DeleteUser(params: IUserDeleteParams) {
    return this.delete<IResponse>(
      "PayrollElementSetup/Delete_Element_Users_Details",
      {
        params: params,
      }
    );
  }

  exportExcel() {
    this.getAll();
  }
}
