import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { payrollElementToMap } from "app/system-setup/payroll-setup/mapper/payroll-element";
import { IPayrollElementModel } from "app/system-setup/payroll-setup/models/payroll-elements";
import { PayrollElementsService } from "app/system-setup/payroll-setup/services/payroll-elements.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-payroll-elements-index",
  templateUrl: "./payroll-elements-index.component.html",
  styleUrls: ["./payroll-elements-index.component.scss"],
})
export class PayrollElementsIndexComponent extends AppHelpers implements OnInit {
  mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
  optionalColomns: string[] = ["variableCode", "display_In_Result"];

  dataSource: MatTableDataSource<IPayrollElementModel>;
  isLoaded: boolean = false;

  constructor(public payrollElementsService: PayrollElementsService) {
    super();
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.currentPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.removePageParams();
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: this.page.pageIndex,
      pageSize: this.page.pageSize,
    };
    this.payrollElementsService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IPayrollElementModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(payrollElementToMap(res.data));
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        }
        else this.errorList(res.message.messages);
      });
  }

  onDelete(payrollElement: IPayrollElementModel): void {
    let msg = this.msgTranslate(payrollElement.ar_Name, payrollElement.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(payrollElement);
    });
  }

  delete(payrollElement: IPayrollElementModel) {
    this.showLoading();
    this.payrollElementsService.Delete(payrollElement.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
      }
      else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }
}
