import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import {
  IPayrollElementModel,
  IPayrollElementUserModel,
} from "../models/payroll-elements";

export function payrollElementToMap(
  payrollElements: IPayrollElementModel[]
): IPayrollElementModel[] {
  return payrollElements.map((payrollElement) => {
    return <IPayrollElementModel>{
      ...payrollElement,
      en_Name: payrollElement.name,
      variableCode: payrollElement.paY_Element_Variable[0].pay_Variable?.code,
      display_In_Result:
        payrollElement.paY_Element_Variable[0].pay_Variable?.display_In_Result,
    };
  });
}

export function payrollElementUserToMap(
  payrollElementUsers: IPayrollElementUserModel[]
): IPayrollElementUserModel[] {
  return payrollElementUsers.map((user) => {
    return <IPayrollElementUserModel>{
      ...user,
      dateFrom: HelpersFunctions.mapDate(user.dateFrom),
      dateTo: HelpersFunctions.mapDate(user.dateTo),
      name: user.uM_Users?.name,
      id: user.userID,
    };
  });
}
