import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { payrollElementUserToMap } from "app/system-setup/payroll-setup/mapper/payroll-element";
import {
	IUserDeleteParams,
	IPayrollElementUserModel,
	IPayrollElementUserRequest,
} from "app/system-setup/payroll-setup/models/payroll-elements";
import { PayrollElementsService } from "app/system-setup/payroll-setup/services/payroll-elements.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-payroll-element-users",
	templateUrl: "./payroll-element-users.component.html",
	styleUrls: ["./payroll-element-users.component.scss"],
})
export class PayrollElementUsersComponent extends AppHelpers implements OnInit {
	@Input() payrollElementId: number;

	form: FormGroup;
	submitted: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"name",
		"dateFrom",
		"dateTo",
		"actions",
	];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	dataSource: MatTableDataSource<IPayrollElementUserModel>;

	constructor(private payrollElementsService: PayrollElementsService) {
		super();
	}

	ngOnInit(): void {
		// if (this.payrollElementId && this.CheckNoBusinessOrCompany()) this.getAll();

		this.form = this.fb.group(
			{
				UserID: [null, [Validators.required]],
				DateFrom: [null, [Validators.required]],
				DateTo: [null, [Validators.required]],
			},
			{
				validator: CustomValidator.compareDates("DateFrom", "DateTo"),
			}
		);
	}

	ngOnChanges(changes: SimpleChanges): void {
		// if (changes.payrollElementId.currentValue > 0) this.getAll();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.payrollElementId && this.checkNoBusinessOrCompany()) this.getAll();
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
		this.payrollElementsService
			.getAllUsersByPayrollElement(this.payrollElementId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPayrollElementUserModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(payrollElementUserToMap(res.data));
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IPayrollElementUserRequest[] = [
				{
					ElementID: this.payrollElementId,
					UserID: this.form.value.UserID,
					DateFrom: HelpersFunctions.mapDateReverse(this.form.value.DateFrom),
					DateTo: HelpersFunctions.mapDateReverse(this.form.value.DateTo),
				},
			];

			this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IPayrollElementUserRequest[]) {
		this.showLoading();
		this.payrollElementsService
			.createUser(model)
			.subscribe((res: IResponse<IPayrollElementUserModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
					this.getAll();
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	onDelete(payrollElementUser: IPayrollElementUserModel): void {
		let msg = this.msgTranslate(payrollElementUser.uM_Users.name);
		this.deleteDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(payrollElementUser);
		});
	}

	delete(payrollElementUser: IPayrollElementUserModel) {
		let params: IUserDeleteParams = {
			elementid: this.payrollElementId,
			userid: payrollElementUser.uM_Users.id,
			from: this.resetDateFormat(payrollElementUser.dateFrom),
			to: this.resetDateFormat(payrollElementUser.dateTo),
		};

		this.showLoading();
		this.payrollElementsService.DeleteUser(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset();
		this.submitted = false;
	}

	resetDateFormat(date: string) {
		// return date as exist in backend
		return date.split("-").reverse().join("-").concat("T00:00:00");
	}
}
