import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { PayrollElementsIndexComponent } from "./components/payroll-elements/payroll-elements-index/payroll-elements-index.component";
import { AddPayrollElementComponent } from "./components/payroll-elements/add-payroll-element/add-payroll-element.component";
import { PaymentTypesComponent } from "./components/payment-types/payment-types.component";
import { LoanRulesComponent } from "./components/loan types/loan-rules/loan-rules.component";
import { LoanTypesComponent } from "./components/loan types/loan-types/loan-types.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";

const routes: Routes = [
  {
    path: "payroll-elements",
    data: {view: ViewsManager.payroll_elements},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: PayrollElementsIndexComponent,
        data: {view: ViewsManager.payroll_elements_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddPayrollElementComponent,
        data: {view: ViewsManager.add_payroll_elements},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:payrollElementId",
        component: AddPayrollElementComponent,
        data: {view: ViewsManager.add_payroll_elements},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "payment-types",
    component: PaymentTypesComponent,
    data: {view: ViewsManager.payment_types},
    canActivate: [CheckIsViewAllowedGuard],
  },

  {
    path: "loans",
    data: {view: ViewsManager.loans},
    children: [
      {
        path: "",
        redirectTo: "loan-types",
        pathMatch: pathMatch.full,
      },
      {
        path: "loan-types",
        component: LoanTypesComponent,
        data: {view: ViewsManager.loan_types},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "loan-rules/:loanTypeId",
        component: LoanRulesComponent,
        data: {view: ViewsManager.loan_rules},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PayrollSetupRoutingModule {}
